
@-webkit-keyframes circle{
    0%{ -webkit-transform:rotate(0deg); transform:rotate(0deg); }
    100%{ -webkit-transform:rotate(360deg); transform:rotate(360deg); }
}
.play_time_bar{
    position: relative;
    margin-top: 13px;
    width: 330px;
    height: 20px;
}
.play_bar{
    height: 5px;
    top: 0;bottom: 0;
    position: absolute;
    margin: auto;
    border-radius: 5px;
}
.play_duration_bar{
    width: 100%;
    border: 1px solid #e9e7e8;
    z-index: 1;
}
.play_buffer_bar{
    background-color: #e9e7e8;
    z-index: 2;
    cursor: pointer;
}
.play_cur_bar{
    background-color: #ee5245;
    border: 1px solid #ee5245;
    z-index: 3;
    cursor: pointer;
}
.play_circle_btn{
    width: 14px;
    height: 14px;
    top: 0;bottom: 0;
    position: absolute;
    margin: auto;
    border-radius: 50%;
    background-color: #fff;
    z-index: 4;
    border: 1px solid #ffe6e4;
    cursor: pointer;
    margin-left: -7px;
}
.music_box{
    width: 400px;
    height: 200px;
}
.play_box{
    position: absolute;
    width: 400px;
    height: 400px;
    top: 0;left: 0;right: 0;bottom: 0;
    margin: auto;
}
.play_btn{
    float: left;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    cursor: pointer;
    background-image: url(http://7xs1hq.com1.z0.glb.clouddn.com/play.gif);
    background-size: contain;
    background-repeat: no-repeat;
}
.play_info_box{
    margin-left: 70px;
}
.play_info_box label{
    font-size: 12px;
}
.play_info_playTiem{
    float: right;
}
.on{
    background-image: url(http://7xs1hq.com1.z0.glb.clouddn.com/stop.gif);
    -webkit-animation:circle 10s infinite linear;
    animation:circle 10s infinite linear;
}



